/* eslint-disable no-lone-blocks */
// jshint esversion: 6
import React from 'react';

import classes from './Input.css';

const input = (props) => {
  let validationError = null;
  if (props.invalid && props.touched) {
    validationError = (
      <p className={classes.ValidationError}>please enter a valid {props.valueType}</p>
    )
  }
  let inputElement = null;
  const inputClasses = [classes.inputElement];

  // that check is independent of the type of the input so I only need to do it once at the beginning.
  if (props.invalid && props.shouldValidate && props.touched) {
    inputClasses.push(classes.Invalid)
  }
  switch (props.elementType) {
    case ('input'):
      {/* I'll join it with a whitespace, so to concatenate all my string classes into one long string where the classes are separated. */}
      inputElement = <input 
        className={inputClasses.join(' ')} 
        {...props.elementConfig} 
        value={props.value}
        onChange={props.changed}/>
      break;
    case ('textarea'):
      inputElement = <textarea 
        className={inputClasses.join(' ')} 
        {...props.elementConfig} 
        value={props.value}
        onChange={props.changed}/>
      break;
    case ('select'):
      inputElement = (
        <select
          className={inputClasses.join(' ')}
          value={props.value}
          onChange={props.changed}>
          {props.elementConfig.options.map(option => {
            return (
              <option key={option.value} value={option.value}>
                {option.displayValue}
              </option>
            )
          })}
        </select> 
      )
      break;
    default:
      inputElement = <input 
        className={inputClasses.join(' ')} 
        {...props.elementConfig} 
        value={props.value}
        onChange={props.changed}/>
  }
  return (
    <div className={classes.Input}>
      <label className={classes.Label}>{props.label}</label>
      {inputElement}
      {validationError}
    </div>
  )
}

export default input;


